<template>
  <div id="stay-apply-right-wrapper">
    <div id="stay-apply-form">
      <stay-apply-information :info="informations[stay - 1]" />
      <div id="airplane-button" @click="stayApply"></div>
    </div>
  </div>
</template>

<script>
import StayApplyInformation from '@/components/StayApply/StayApplyInformation'
export default {
  name: 'StayApplyRight',
  components: {
    StayApplyInformation
  },
  data: function () {
    return {
      informations: [
        {
          title: '',
          src: require('@/assets/Stay_application/icon/ic_fri_home.png'),
          intro:
          '  \n' +
          '     8 30\n' +
          '    8 30\n' +
          '   .'
        },
        {
          title: '',
          src: require('@/assets/Stay_application/icon/ic_sat_home.png'),
          intro:
          '  \n' +
          '  9  \n' +
          '  8 30 \n' +
          '  .'
        },
        {
          title: '',
          src: require('@/assets/Stay_application/icon/ic_sat_dor.png'),
          intro:
          '  \n' +
          '     8 30\n' +
          '    8 30\n' +
          '   .'
        },
        {
          title: '',
          src: require('@/assets/Stay_application/icon/ic_stay.png'),
          intro:
          '  \n' +
          '   .'
        }
      ]
    }
  },
  computed: {
    stay: function () {
      return this.applyStatus.stay
    }
  },
  props: {
    applyStatus: {type: Object}
  },
  methods: {
    stayApply: function () {
      this.$http.post('/student/apply/stay', {
        value: this.stay
      }, {
        headers: {
          Authorization: 'JWT ' + this.$cookie.getCookie('JWT')
        }
      })
      .then(response => {
        if (response.status === 201) {
          if (this.stay === 1) {
            alert('  .')
          } else if (this.stay === 2) {
            alert('  ')
          } else if (this.stay === 3) {
            alert('  ')
          } else if (this.stay === 4) {
            alert('  ')
          } else {
            alert('   ')
          }
        } else if (response.status === 204) {
          alert('    .')
        }
      }).catch(() => {
        alert('  .')
      })
    }
  }
}
</script>

<style scoped>
#stay-apply-right-wrapper {
  width: 100%;
  height: 100%;
  background-image: url('../../assets/Stay_application/background/bg_homecoming_application.svg');
  background-size:cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

#stay-apply-form {
  position: relative;
  width: 400px;
  height: 580px;
  top: 50%;
  left: 0;
  margin: 0 auto;
  transform: translate(0, -50%);
}

#airplane-button {
  width: 100px;
  height: 100px;
  margin: 0 auto;
  bottom: 0;
  margin-top: 70px;
  background-image: url('../../assets/Stay_application/ic_enter.png');
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
}

#airplane-button:hover {
  animation: jelly 1s;
}

@keyframes jelly {
  10% {
      transform: none;
  } 20% {
      transform: skewX(-15deg) skewY(-15deg);
  } 30% {
      transform: skewX(5.51876deg) skewY(5.51876deg);
  } 40% {
      transform: skewX(-2.03045deg) skewY(-2.03045deg);
  } 50% {
      transform: skewX(0.74704deg) skewY(0.74704deg);
  } 60% {
      transform: skewX(-0.27485deg) skewY(-0.27485deg);
  } 70% {
      transform: skewX(0.10112deg) skewY(0.10112deg);
  } 80% {
      transform: skewX(-0.0372deg) skewY(-0.0372deg);
  } 90% {
      transform: skewX(-0.01369deg) skewY(-0.01369deg);
  } 100% {
      transform: none;
  }
}

</style>



