/* eslint-disable */
import * as types from '../mutation-types'
import router from '@/router'

const state = {
  isLogin: false
}

const getters = {
  isLogin: state => state.isLogin
}

const actions = {
  login ({commit, getters}, payload) {
    if(!getters.isLogin) {
      this._vm.$http.post('/student/auth', {
        id: payload.userName,
        password: payload.password
      })
      .then(response => {
        if (response.status === 201) {
          alert(' .')
          if (payload.checked) {
            this._vm.$cookie.setCookie('JWT', response.data['accessToken'], 30)
          } else {
            this._vm.$cookie.setCookie('JWT', response.data['accessToken'])
          }
          router.go(0)
        }
      }).catch(error => {
        alert(' .')
      })
    }
  },
  logout ({commit, getters}) {
    if(getters.isLogin) {
      alert(' .')
      this._vm.$cookie.deleteCookie('JWT')
      commit(types.SET_LOGIN_STATUS, { isLogin: false })
    } else {
      alert(' .')
    }
  }
}

const mutations = {
  [types.SET_LOGIN_STATUS] (state, payload) {
    state.isLogin = payload.isLogin
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}


